
import iHeader from "@/components/header/header-r.vue"
import { TabContainer, TabContainerItem,Toast,MessageBox } from "mint-ui"
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
name: "my-post-detail",
data() {
return {
users:localStorage.getItem('users')?JSON.parse(localStorage.getItem('users')):'',
defaultImg: 'this.src="' + require('@/assets/images/head.png') + '"',
title:"详情",
is_r:true,
msgdata:'',
textmodel:'',
readonly:true,
id:'',
touserid:'',
leftonoff:false,
dianzan:true,
show5:false,
};
},
components: {
ConfirmPlugin,Confirm,
iHeader,
},
created() {

},
mounted(){
this.getlist();

},
methods:{
onConfirm5 (value) {


this.buttonAddTodo();
},
onShow5 () {

},
onConfirm (msg) {
if (msg) {
alert(msg)
}
},
onCancel () {
console.log('on cancel')
},
getlist(){
this.$axios.post('/api/Post/post_detail',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
post_id:this.$route.query.id
})}`).then((res)=>{

if(res.code==200){
this.msgdata=res.data;
}
}).catch((err)=>{throw err});
},
buttonAddTodo(id,touserid){
this.show5=true;
if(!this.textmodel){
Toast('请输入内容');
return false;
};
if(!this.touserid){
this.$axios.post('/api/Post/do_comment',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
post_id:this.id,
content:this.textmodel,
})}`).then((res)=>{
if(res.code==200){
this.getlist();

this.textmodel='';
Toast(res.msg);
}else{
Toast(res.msg)
}
}).catch((err)=>{throw err});
return false;
}else{
this.$axios.post('/api/Post/do_comment',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
post_id:this.id,
content:this.textmodel,
pid:this.touserid,
})}`).then((res)=>{
if(res.code==200){
this.getlist();
this.textmodel='';
Toast(res.msg);
this.touserid='';
}else{
Toast(res.msg)
}
}).catch((err)=>{throw err});
}
},
buttonTodo(id,touserid,comment){
this.id=id;
if(!!touserid){this.touserid=touserid;};
this.leftonoff=false;
if(comment==1){
MessageBox.confirm('确定删除该评论?').then(action => {
this.$axios.post('/api/Post/del_my_comment',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
id:this.touserid,
})}`).then((res)=>{
if(res.code==200){
Toast(res.msg);
this.touserid='';
this.getlist();
}else{
Toast(res.msg)
}
}).catch((err)=>{throw err});
});
return false;
}else {
this.show5=true;
return false;
}
},
leftid(){
let getzan=localStorage.getItem('dianzan')
if(getzan=='true'){
this.dianzan=true;
}
else if(getzan=='false'){
this.dianzan=false;
}
this.leftonoff=!this.leftonoff;
},
zan(id,index){
this.$axios.post('/api/Post/do_likenum',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
post_id:id
})}`).then((res)=>{
if(res.code==200){
this.leftonoff=false;
if(this.dianzan==true){
this.dianzan=false;
localStorage.setItem('dianzan',false)
}else if(this.dianzan==false){
this.dianzan=false;
localStorage.setItem('dianzan',true)
};
this.getlist();
}else{
Toast(res.msg)
}
}).catch((err)=>{throw err});
},
},
watch:{









},
directives : {
"focus" : function (el,binding) {
if(binding.value){
el.focus();
}
}
},
};



